.homepage_main {
    /* display:block; */
    /* text-align:center; */
    position: absolute;
    margin-left:auto;
    margin-right:auto;
    margin-top:-320px;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  .checkboxlabel {
    text-align: center;
    font-size: 1.25em;
    margin-top: 1.5em;
  }
  
  .patientName {
    width: 50vw;
  }
  .eachRow {
    background-color: aqua;
    /* border-color: blue; */
    border-radius: 25px;
    padding-left: 25px;
    padding-right: 25px;
  }
  .searchbox {
    align-content: center;
    align-items: center;
    /* margin-left: 1px; */
    margin-bottom: -10px;
    /* background-color: rgb(129, 171, 235); */
    padding-left:0px;
    /* padding-top: 30px;
    padding-bottom:30px; */
    /* width: 160%; */
  }
  .odd {
    background-color: rgb(0, 4, 255);
    /* border-color: blue; */
    /* border-radius: 25px;
    padding-left: 25px;
    padding-right: 25px; */
  }
  .empty-div-above {
    margin-top:6%;
  }
  .empty-div {
    margin-bottom:10px;
  }
  .test-class {
    color: red;
    background-color: rgb(129, 171, 235);
  }
  
  .invisible-text {
    background: transparent;
    border: none;
    width:0vw;
  }
  .dispay-flex {
    display: flex;
  }
  .last5 {
    padding: 30px;
    display:block;
  
    color: white;
    text-align:center;
    position: absolute;
    top: -31px;
    right: 10px;
    ;
  }
  
  .last5-button {
    height:44px;
    margin-right: -90px;
  }
  .searchResNotFound {
    text-align: center;
  }
  .topnav {
    display: flex;
    align-items: center;
    margin: auto;
    overflow: hidden;
    background-color: #e9e9e9;
    height:44px;
    font-size: small;
    box-shadow: 10px 10px 5px grey;
  }
  
  .topnav input[type=text] {
    /* padding-top: 6px; */
    margin-top: 1.5px;
    margin-left: 1.5px;
    /* margin-bottom: 15px;  */
    font-size: 17px;
    border: none;
    height:40px;
    width: 600px;
  }
  
  .SearchIcon {
    height:40px;
  }
  
  .topnav .search-container button {
    float: right;
    padding: 6px 10px;
    /* margin-top: 8px; */
    margin-right: 16px;
    background: #ddd;
    font-size: 17px;
    border: none;
    cursor: pointer;
    height:44px;
  }
  
  .topnav .search-container button:hover {
    background: #ccc;
  }
  .test-boxy1 {
    color:crimson;
  }

  #customers {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  #customers td, #customers th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  #customers tr:nth-child(even){background-color: #f2f2f2;}
  
  #customers tr:hover {background-color: #ddd;}
  
  #customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #253d8b;
    color: white;
  }

  .searchPOIbox {
    margin: 1%;
    box-shadow: 10px 10px 5px grey;
  }
  
  